<template>
  <div class="hello">
    <h1>{{ msg }}</h1>
    <a href="javascript:;" @click="show" class="link">Show device Motion</a>
    <a class="link" @click="back">返回</a>
  </div>
</template>

<script>
  export default {
    name: 'motion',
    data () {
      return {
        msg: 'Device Motion'
      }
    },
    mounted () {
      document.title = 'Device Motion'
    },
    methods: {
      show () {
        if (window.navigator.accelerometer) {
          window.navigator.accelerometer.getCurrentAcceleration((acceleration) => {
            alert('Acceleration X: ' + acceleration.x + '\n' +
              'Acceleration Y: ' + acceleration.y + '\n' +
              'Acceleration Z: ' + acceleration.z + '\n' +
              'Timestamp: ' + acceleration.timestamp + '\n')
          }, () => {
            alert('onError!')
          })
        }
      },
      back () {
        let vm = this
        vm.$router.go(-1)
        return false
      }
    }
  }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style type="text/scss" scoped>
  h1, h2 {
    font-weight: normal;
    font-size: .6rem;
  }

  .link {
    display: block;
    color: #42b983;
    font-size: .5rem;
  }

  .photo {
    display: block;
    width: 6rem;
    height: auto;
    margin: .2rem auto;
  }
</style>



